@import 'components/variables';

.section-welcome {
  background-image: url(../images/bg-welcome.png);
  background-position: bottom center;
  background-repeat: no-repeat;
  background-size: cover;
  padding-top: 100px;
  padding-bottom: 6rem;
  display: flex;
  align-items: center;
  border-bottom-right-radius: 40px;
  position: relative;
  @media (min-width: 576px) {
    border-bottom-right-radius: 0;
  }
  @media (min-width: 992px) {
    padding-top: 150px;
  }
  .title {
    text-align: center;
    @media (min-width: 768px) {
      text-align: start;
    }
  }
  .trasfondo {
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    background: $yellow;
    z-index: -1;
  }
}
.content-reserva {
  background-color: $yellow;
  padding: 1.5rem;
  text-align: center;
  position: relative;
  @media (min-width: 576px) {
    border-radius: 15px;
  }
  img {
    position: absolute;
    top: 0.6rem;
    right: 0.6rem;
    width: 15px;
  }
  a {
    text-decoration: none;
    color: $text-gray;
    transition: all .5s;
    &:hover {
      color: $blue;
    }
  }
}
.section-contacto {
  background-color: #F5F8FD;
  .container {
    transform: translateY(-4rem);
    @media (min-width: 576px) {
      background: linear-gradient(to bottom, rgba(150, 221, 241, 1) 0%, rgba(255, 255, 255, 1) 8%, rgba(255, 255, 255, 1) 12%, rgba(255, 255, 255, 1) 100%);
      border-radius: 15px;
    }
    .content-contacto {
      padding: 4rem 1rem;
      @media (min-width: 576px) {
        padding: 4rem 3rem;
      }
      iframe {
        width: 100%;
        height: 300px;
      }
      .col-izq {
        order: 2;
        @media (min-width: 768px) {
          order: 1;
        }
        .content-map {
          background-color: #353A34;
          padding: 1rem 0;
          border-bottom-left-radius: 10px;
          border-bottom-right-radius: 10px;
          p {
            color: $white;
          }
          .fa-map-marker-alt {
            color:$blue;
          }
        }
      }
      .col-der {
        order: 1;
        @media (min-width: 768px) {
          padding: 0 3rem;
          order: 2;
        }
        .text-yellow-contacto {
          font-size: 2rem;
          font-weight: 600;
          color: $yellow;
          text-align: center;
        }
      }
    }
  }
}